
export const hash = (str) => {
    // compute random hash
    var hash = 0, i, chr;

    if (str.length === 0)
        return hash;

    for (i = 0; i < str.length; i++) {
        chr = str.charCodeAt(i);
        hash = ((hash << 5) - hash) + chr;
        hash |= 0; // Convert to 32bit integer
    }

    hash = hash * hash ** 11;
    hash = hash % 100000000;

    return hash;    
}